@import url("https://fonts.googleapis.com/css2?family=Tenor+Sans&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  display: block;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 400;
}

.container {
  max-width: 1190px;
  margin: 0 auto;
  padding: 0 15px;
}

.container-fluid {
  max-width: 1920px;
  margin: 0 auto;
}

body {
  font-family: "Tenor Sans", sans-serif;
  font-weight: 400;
}

.btn {
  font-family: "Tenor Sans", sans-serif;
  background-color: #f3d5ae;
  border-radius: 4px;
  padding: 14px 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  cursor: pointer;
  transition: 0.2s;
}
.btn:hover {
  background-color: #f6c483;
}

.h2 {
  font-size: 32px;
  line-height: 130%;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #262626;
}

.header {
  background-color: #7f713e;
}
.header__nav {
  display: flex;
  align-items: center;
  padding: 16px 0px;
}
@media (max-width: 1100px) {
  .header__nav {
    justify-content: space-between;
    height: 60px;
  }
}
.header__logo {
  max-width: 143px;
}
@media (max-width: 1100px) {
  .header__logo {
    display: none;
  }
}
.header__logo-mobile {
  max-width: 33px;
  display: none;
}
@media (max-width: 1100px) {
  .header__logo-mobile {
    display: block;
  }
}
.header__logo-img {
  max-width: 100%;
}
.header__menu {
  display: flex;
  gap: 8px;
  margin: 0 auto;
}
@media (max-width: 1100px) {
  .header__menu {
    display: none;
  }
}
.header__menu-link, .header__phone {
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: #fff;
}
.header__menu-link {
  padding: 6px 10px;
  transition: 0.2s;
}
.header__menu-link:hover {
  border: 1px solid #fff;
  border-radius: 100px;
}
.header__phone:hover {
  border-bottom: 1px solid #fff;
}
.header__social {
  display: flex;
  gap: 16px;
  margin-left: 32px;
}
@media (max-width: 1100px) {
  .header__social {
    display: none;
  }
}
.header__social-link {
  transition: 0.2s;
}
.header__social-link:hover {
  transform: translateY(-3px);
}
.header__burger {
  display: none;
  width: 24px;
  height: 21px;
  background-image: url(../img/burger.svg);
  background-repeat: no-repeat;
  transition: 0.2s;
}
@media (max-width: 1100px) {
  .header__burger {
    display: block;
  }
}
.header__burger.close {
  background-image: url(../img/burger-close.svg);
}

.header-main {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-color: transparent;
  transition: background-color 0.4s;
}

@media (max-width: 1100px) {
  .header-main.show {
    background-color: #7f713e;
  }
}

.mobile-menu {
  position: fixed;
  top: 60px;
  left: 0;
  width: 100%;
  padding-bottom: 16px;
  transform: translateX(120%);
  background-color: #7f713e;
  transition: 0.4s;
}
.mobile-menu__item {
  text-align: center;
  margin-top: 16px;
}
.mobile-menu__link {
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: #fff;
}
.mobile-menu__social {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-top: 24px;
}
.mobile-menu.show {
  transform: translateX(0%);
}

.intro {
  padding: 248px 0;
  background-image: url(../img/intro-bg.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media (max-width: 480px) {
  .intro {
    background-image: url(../img/intro-bg-mobile.jpg);
    padding: 245px 0;
  }
}
@media (max-width: 500px) {
  .intro__inner {
    max-width: 324px;
    margin: 0 auto;
  }
}
.intro__title {
  font-size: 48px;
  line-height: 130%;
  text-align: center;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #fff;
  margin-bottom: 16px;
}
@media (max-width: 600px) {
  .intro__title {
    font-size: 32px;
  }
}
.intro__text {
  font-size: 24px;
  line-height: 150%;
  text-align: center;
  color: #fff;
  margin: 0 auto;
  margin-bottom: 40px;
  max-width: 477px;
}
@media (max-width: 600px) {
  .intro__text {
    font-size: 16px;
  }
}
@media (max-width: 350px) {
  .intro__text {
    font-size: 14px;
  }
}
.intro__btn {
  font-size: 16px;
  line-height: 150%;
  text-align: center;
  color: #513f29;
  margin: 0 auto;
}

.about {
  margin-top: 120px;
}
@media (max-width: 500px) {
  .about {
    margin-top: 64px;
  }
}
.about__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 32px;
}
@media (max-width: 950px) {
  .about__inner {
    flex-wrap: wrap;
    justify-content: center;
  }
}
.about__col--1 {
  max-width: 560px;
}
@media (max-width: 950px) {
  .about__col--1 {
    order: 1;
  }
}
.about__img {
  max-width: 100%;
}
.about__col--2 {
  max-width: 460px;
}
@media (max-width: 950px) {
  .about__col--2 {
    max-width: 560px;
  }
}
.about__title {
  margin-bottom: 42px;
}
@media (max-width: 500px) {
  .about__title {
    font-size: 24px;
    margin-bottom: 23px;
  }
}
.about__text {
  font-size: 24px;
  line-height: 150%;
  color: #262626;
  margin-top: 33px;
}
@media (max-width: 500px) {
  .about__text {
    font-size: 16px;
    margin-top: 23px;
  }
}

.running-line {
  background-color: #7f713e;
  padding: 24px 0;
  overflow: hidden;
  display: flex;
  margin-top: 120px;
}
@media (max-width: 770px) {
  .running-line {
    display: none;
  }
}
.running-line:hover .running-line__text {
  animation-play-state: paused;
}
.running-line__text {
  font-size: 24px;
  line-height: 120%;
  text-transform: uppercase;
  color: #fff;
  display: flex;
  gap: 40px;
  padding-right: 40px;
  white-space: nowrap;
  transform: translateX(0%);
  animation: running-text 15s infinite linear;
}
@keyframes running-text {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-100%);
  }
}
.catalog {
  margin-top: 120px;
}
@media (max-width: 650px) {
  .catalog {
    margin-top: 64px;
  }
}
.catalog__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 48px;
}
@media (max-width: 650px) {
  .catalog__header {
    margin-bottom: 30px;
  }
}
@media (max-width: 500px) {
  .catalog__title {
    font-size: 24px;
  }
}
.catalog__more {
  display: flex;
  gap: 8px;
  align-items: center;
}
@media (max-width: 500px) {
  .catalog__more {
    display: none;
  }
}
.catalog__more:hover svg {
  fill: #c39f1d;
}
.catalog__more-text {
  font-size: 20px;
  line-height: 130%;
  letter-spacing: 0.02em;
  color: #7f713e;
  transition: 0.2s;
}
.catalog__more-text:hover {
  color: #c39f1d;
}
.catalog__more svg {
  fill: #7f713e;
  transition: 0.2s;
}
.catalog__gallery {
  display: grid;
  gap: 40px;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 254px 223px 254px;
  grid-auto-rows: 254px;
}
@media (max-width: 900px) {
  .catalog__gallery {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, 254px);
    gap: 24px;
  }
}
@media (max-width: 650px) {
  .catalog__gallery {
    grid-template-columns: 1fr;
    gap: 24px 0;
  }
}
@media (max-width: 500px) {
  .catalog__gallery {
    grid-template-rows: repeat(7, auto);
  }
}
.catalog__card {
  border-radius: 4px;
  background-repeat: no-repeat;
  padding: 48px 15px 15px 32px;
}
@media (max-width: 650px) {
  .catalog__card {
    padding: 24px 15px 24px 24px;
  }
}
.catalog__card--1 {
  background: linear-gradient(90deg, rgba(79, 64, 43, 0.9) 0.66%, rgba(79, 64, 43, 0.5) 53.52%, rgba(79, 64, 43, 0) 100%), url(../img/catalog-1.jpg);
  grid-column: 1/3;
}
@media (max-width: 650px) {
  .catalog__card--1 {
    grid-column: auto;
  }
}
.catalog__card--2 {
  background: linear-gradient(90deg, rgba(79, 64, 43, 0.9) 0.66%, rgba(79, 64, 43, 0.5) 53.52%, rgba(79, 64, 43, 0) 100%), url(../img/catalog-2.jpg);
}
.catalog__card--3 {
  background: linear-gradient(90deg, rgba(79, 64, 43, 0.9) 0.66%, rgba(79, 64, 43, 0.5) 53.52%, rgba(79, 64, 43, 0) 100%), url(../img/catalog-3.jpg);
}
.catalog__card--4 {
  background: linear-gradient(90deg, rgba(79, 64, 43, 0.9) 0.66%, rgba(79, 64, 43, 0.5) 53.52%, rgba(79, 64, 43, 0) 100%), url(../img/catalog-4.jpg);
}
.catalog__card--5 {
  background: linear-gradient(90deg, rgba(79, 64, 43, 0.9) 0.66%, rgba(79, 64, 43, 0.5) 53.52%, rgba(79, 64, 43, 0) 100%), url(../img/catalog-5.jpg);
}
.catalog__card--6 {
  background: linear-gradient(90deg, rgba(79, 64, 43, 0.9) 0.66%, rgba(79, 64, 43, 0.5) 53.52%, rgba(79, 64, 43, 0) 100%), url(../img/catalog-6.jpg);
}
.catalog__card--7 {
  background: linear-gradient(90deg, rgba(79, 64, 43, 0.9) 0.66%, rgba(79, 64, 43, 0.5) 53.52%, rgba(79, 64, 43, 0) 100%), url(../img/catalog-7.jpg);
  grid-column: 2/-1;
}
@media (max-width: 650px) {
  .catalog__card--7 {
    grid-column: auto;
  }
}
.catalog__card {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.catalog__card-title {
  font-size: 24px;
  font-weight: 400;
  line-height: 130%;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #fff;
  max-width: 170px;
  margin-bottom: 8px;
}
@media (max-width: 650px) {
  .catalog__card-title {
    font-size: 20px;
  }
}
.catalog__card-text {
  font-size: 16px;
  line-height: 150%;
  color: #fff;
  max-width: 300px;
  margin-bottom: 16px;
}
@media (max-width: 650px) {
  .catalog__card-text {
    font-size: 15px;
  }
}
.catalog__card-link {
  font-size: 16px;
  line-height: 150%;
  color: #f3d5ae;
  transition: 0.2s;
}
@media (max-width: 650px) {
  .catalog__card-link {
    font-size: 15px;
  }
}
.catalog__card-link:hover {
  color: #f5b869;
}
.catalog__card-link svg {
  stroke: #f3d5ae;
  transition: 0.2s;
  margin-left: 5px;
}
.catalog__card-link:hover svg {
  stroke: #f5b869;
}

.popular {
  margin-top: 120px;
}
@media (max-width: 600px) {
  .popular {
    margin-top: 11px;
  }
}
.popular__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 48px;
}
@media (max-width: 600px) {
  .popular__header {
    margin-bottom: 30px;
  }
}
@media (max-width: 800px) {
  .popular__title {
    font-size: 28px;
  }
}
@media (max-width: 500px) {
  .popular__title {
    font-size: 24px;
  }
}
.popular__slider-navigation {
  display: flex;
  gap: 8px;
}
@media (max-width: 500px) {
  .popular__slider-navigation {
    display: none;
  }
}
.popular__slider-navigation-prev, .popular__slider-navigation-next {
  position: static;
  margin-top: 0;
  width: 40px;
  height: 40px;
  border: 1px solid #7f713e;
  border-radius: 4px;
}
.popular__slider-navigation-prev svg, .popular__slider-navigation-next svg {
  stroke: #7f713e;
}
.popular__slider-navigation-prev::after, .popular__slider-navigation-next::after {
  display: none;
}
@media (max-width: 500px) {
  .popular__content {
    padding: 0 0 0 16px;
  }
}
.popular__slider-wrapper {
  left: -68px;
}
@media (max-width: 800px) {
  .popular__slider-wrapper {
    left: 0;
  }
}
.popular__card {
  min-height: 543px;
}
.popular__card-inner {
  background-color: #f9f9f9;
  height: 459px;
  padding: 32px;
  display: flex;
  flex-direction: column;
  border-radius: 14px;
  position: relative;
}
.popular__card-img-wrap {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.popular__card-img {
  max-width: 100%;
}
.popular__card-title {
  margin-bottom: 4px;
  font-size: 24px;
  line-height: 130%;
  color: #262626;
}
.popular__card-text {
  margin-bottom: 24px;
  font-size: 16px;
  line-height: 150%;
  color: #595959;
}
.popular__card-price {
  margin-bottom: 24px;
  font-size: 24px;
  line-height: 28px;
  color: #7f713e;
}
.popular__card-colors {
  display: flex;
  gap: 8px;
}
.popular__card-colors-item {
  width: 16px;
  height: 16px;
  background-color: #7d4d29;
  border-radius: 2px;
}
.popular__card-bottom {
  background-color: #f9f9f9;
  height: 84px;
  padding: 0 32px 32px 32px;
  opacity: 0;
  border-radius: 0 0 14px 14px;
  transition: 0.4s;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
.popular__card-inner:hover .popular__card-bottom, .popular__card-bottom:hover {
  opacity: 1;
  transform: translateY(84px);
}
.popular__card:hover .popular__card-inner {
  border-radius: 14px 14px 0 0;
}
.popular__card-btn {
  font-size: 16px;
  line-height: 150%;
  color: #513f29;
  width: 100%;
}